import interfaceurl from '@/utils/interface.js'
//引入store的内容
import {
	mapState,
	mapMutations,
	mapActions
} from 'vuex'
export default {
	computed: mapState(['userInfo']),
	data() {
		return {
			params: {
				name: '',
				defaultSign: 0
			}
		}
	},
	onLoad() {
		
	},
	methods: {
		addEquipment() {
			let that = this;
			that.params.userId = that.userInfo.userId;
			interfaceurl.checkAuth(interfaceurl.equipmentSave, that.params).then((res) => {
				uni.showToast({
				    title: '添加设备成功',
				    icon: 'success',
				    duration: 2000
				});
			});
		}
	}
}
